@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;500;600;700;800&display=swap');

/* Variabel Tema */
:root {
    --header-height: 3rem;
    --hue: 0;
    --sat: 0%;
    --title-color: hsl(var(--hue), var(--sat), 20%);
    --title-color-dark: hsl(75, 46%, 51%);
    --text-color: hsl(var(--hue), var(--sat), 46%);
    --body-color: hsl(var(--hue), var(--sat), 98%);
    --container-color: #fff;
    --body-font: "Nunito", sans-serif;
    --big-font-size: 3.5rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.813rem;
    --smallest-font-size: 0.625rem;
    --font-normal: 400;
    --font-medium: 500;
    --font-semibold: 600;
    --font-bold: 700;
    --mb-0-25: .25rem;
    --mb-0-5: 0.5rem;
    --mb-0-75: 0.75rem;
    --mb-1: 1rem;
    --mb-1-5: 1.5rem;
    --mb-2: 2rem;
    --mb-2-5: 2.5rem;
    --mb-3: 3rem;
    --z-tooltip: 10;
    --z-fixed: 100;
    --z-modal: 1000;
}

@media screen and (max-width: 992px) {
    :root {
        --big-font-size: 2.75rem;
        --h1-font-size: 1.5rem;
        --h2-font-size: 1.25rem;
        --h3-font-size: 1rem;
        --normal-font-size: 0.938rem;
        --small-font-size: 0.813rem;
        --smaller-font-size: 0.75rem;
        --smallest-font-size: 0.625rem;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body, button, input, textarea {
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
}

/* Light Mode */
body.light-mode {
    background-color: #ffffff;
    color: #000000;
}

header.light-mode {
    background-color: #f0f0f0; /* Light mode header background */
}

footer.light-mode {
    background-color: #f0f0f0; /* Light mode footer background */
}

/* Dark Mode */
body.dark-mode {
    background-color: #000000;
    color: #ffffff;
}

header.dark-mode {
    background-color: #333333; /* Dark mode header background */
}

footer.dark-mode {
    background-color: #333333; /* Dark mode footer background */
}

/* Global Styles */
body {
    background-color: var(--body-color);
    color: var(--text-color);
}

h1, h2, h3 {
    color: var(--title-color);
    font-weight: var(--font-semibold);
}

ul {
    list-style: none;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
    border: none;
    outline: none;
}

img {
    max-width: 100%;
    height: auto;
}

.section {
    padding: 6rem 0 2rem;
}

.section__subtitle {
    display: block;
    font-size: var(--small-font-size);
    margin-bottom: 4rem;
}

.section__title, .section__subtitle {
    text-align: center;
}

.container {
    max-width: 968px;
    margin-left: auto;
    margin-right: auto;
}

.grid {
    display: grid;
    gap: 1.5rem;
}

.button {
    display: inline-block;
    background-color: var(--title-color);
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: var(--font-medium);
}

.button:hover {
    background-color: var(--title-color-dark);
}

.button__icon {
    margin-left: var(--mb-0-5);
}

.button--flex {
    display: inline-flex;
    align-items: center;
}

@media screen and (max-width: 992px) {
    .container {
        margin-left: var(--mb-1-5);
        margin-right: var(--mb-1-5);
    }

    .button {
        padding: 1rem 1.75rem;
    }

    .button__icon {
        width: 22px;
        height: 22px;
    }
}

@media screen and (max-width: 768px) {
    body {
        margin: 00 var(--header-height) 0;
    }
    .section {
        padding: 2rem 0 4rem;
    }

    .section__subtitle {
        margin-bottom: var(--mb-3);
    }
}

@media screen and (max-width: 576px) {
}

@media screen and (max-width: 350px) {
    :root {
        --big-font-size: 2.25rem;
    }

    .container {
        margin-left: var(--mb-1);
        margin-right: var(--mb-1);
    }
}

/* Judul dan Ikon untuk Tema Light */
body.light-mode h1, body.light-mode h2, body.light-mode h3, body.light-mode h4, body.light-mode h5, body.light-mode h6 {
    color: #000000;
}

body.light-mode .nav__icon {
    color: #000000;
}

/* Judul dan Ikon untuk Tema Dark */
body.dark-mode h1, body.dark-mode h2, body.dark-mode h3, body.dark-mode h4, body.dark-mode h5, body.dark-mode h6 {
    color: #ffffff;
}

body.dark-mode .nav__icon {
    color: #ffffff;
}

/* Kotak-Kotak untuk Tema Light */
.card {
    background-color: #ffffff;
    color: #000000;
    border: 1px solid #cccccc;
}

/* Kotak-Kotak untuk Tema Dark */
body.dark-mode .card {
    background-color: #333333;
    color: #ffffff;
    border: 1px solid #444444;
}

/* Style untuk button switch mode */
.theme-switch-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
    color: inherit;
}

/* Tema Light */
body.light-mode {
    background-color: #ffffff;
    color: #000000;
}

header.light-mode {
    background-color: #f0f0f0; /* Contoh warna */
}

footer.light-mode {
    background-color: #f0f0f0; /* Contoh warna */
}

.button {
    display: inline-block;
    background-color: var(--title-color);
    color: var(--container-color);
    padding: 1.25rem 2rem;
    border-radius: 1rem;
    font-weight: var(--font-medium);
    transition: background-color 0.3s ease, cursor 0.3s ease;
}

.button:hover {
    background-color: var(--title-color-dark);
    cursor: pointer;
}

/* Tema Dark */
body.dark-mode {
    background-color: #000000;
    color: #ffffff;
}

header.dark-mode {
    background-color: #333333; /* Contoh warna */
}

footer.dark-mode {
    background-color: #333333; /* Contoh warna */
}

body.dark-mode .button {
    background-color: var(--title-color-dark);
    color: var(--container-color);
}

body.dark-mode .button:hover {
    background-color: var(--title-color);
    cursor: pointer;
}
